* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}
body {
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(54, 54, 54);
    min-height: 100vh;
}

.app {
    width: 25rem;
    height: 35rem;
    background: url(bg.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 1rem;
    position: relative;
    box-shadow: 2px 3px 17px rgba(0, 0, 0, 0.664);
    border-radius: 0.6rem;
    color: #fff;
}

.app .search {
    text-align: center;
    padding: 0.6rem 0;
}
.search input {
    width: 100%;
    height: 3rem;
    border-radius: 0.6rem;
    padding: 0.7rem 1.5rem;
    border: none;
    outline: none;
    color: #000;
    font-size: 1.2rem;
}
.search input::placeholder {
    color: rgb(177, 177, 177);
}

.app .container {
    padding: 0.6rem;
    border-radius: 10px;
}
p {
    font-size: 1.6rem;
    text-align: center;
}
h1 {
    font-size: 5rem;
    text-align: center;
}

.app .top {
    width: 100%;
    margin: 1rem auto;
}
.top .location p {
    padding-bottom: 20px;
    font-size: 2rem;
}
.top .temp h1 {
    padding-bottom: 20px;
}
.top .desc p {
    font-size: 2rem;
}

.container .bottom {
    display: flex;
    justify-content: space-evenly;
    position: absolute;
    bottom: 0;
    margin: 1rem -1.5rem;
    padding: 1rem;
    width: 100%;
}
.bottom P {
    font-size: 1.1rem;
}
.bottom .feels,
.bottom .humidity,
.bottom .wind {
    border-bottom: 2px solid rgb(177, 177, 177);
}
